* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e9e0e0;
}

.breed-select {
  z-index: 200;
  width: 250px;
  position: absolute;
  top: 5%;
  left: 10%;
  transform: translate(-5%, -50%);
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #00f;
  border-color: #00f green red pink;
  border-radius: 50%;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 45%;
  left: 45%;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  color: #000;
  margin: 50px 0 30px;
  padding: 0 5px;
  font-size: 20px;
  font-style: oblique;
}

